.seccion-con-imagen-fija {
  width: 100%;
  min-height: 500px;
  padding: 20px;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.imagen-fija-fondo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('../img/paseo-nautico.jpg');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  z-index: -1;
}

.contenido-de-la-seccion {
  position: relative;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 40px;
  border-radius: 8px;
  max-width: 1200px;
  width: 100%;
  overflow: hidden;
  transition: height 0.6s ease-in-out;
  height: auto;
}

.titulo {
  font-size: 3.4rem;
  margin: 0;
  color: #333;
}

.contenido-de-la-seccion h2 {
  margin-bottom: 0rem;
}

.servicios {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  flex-wrap: wrap;
  text-align: center;
  margin-top: 30px;
  opacity: 1;
  transition: opacity 0.5s ease;
}

.servicio-fijo {
  flex: 1 1 250px;
  background-color: rgba(255, 255, 255, 0.85);
  padding: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Íconos */
.servicio-fijo i {
  font-size: 2.5rem;
  color: #222;
  margin-bottom: 15px;
}

/* Título */
.servicio-fijo h3 {
  margin-bottom: 10px;
  color: #111;
  font-size: 1.2rem;
  margin-top: 1rem;
  text-align: center;
}

/* Párrafos */
.servicio-fijo p {
  font-size: 1rem;
  color: #444;
}

/* Ocultos por JavaScript */
.oculto {
  display: none;
}

/* Animaciones: solo en pantallas grandes */
@media (min-width: 769px) {
  .fade-in {
    animation: fadeIn 0.5s forwards;
  }

  .fade-out {
    animation: fadeOut 0.5s forwards;
  }
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to   { opacity: 1; transform: translateY(0); }
}

@keyframes fadeOut {
  from { opacity: 1; transform: translateY(0); }
  to   { opacity: 0; transform: translateY(20px); }
}

/* --- RESPONSIVE para móviles --- */
@media (max-width: 768px) {
  .contenido-de-la-seccion {
    padding: 20px;
  }

  .titulo {
    font-size: 2.2rem;
  }

  .servicio-fijo {
    flex: 1 1 100%;
    padding: 1.5rem;
  }

  // .servicio-fijo i,
  // .servicio-fijo p {
  //   //display: none !important; /* Ocultamos íconos y párrafos */
  // }

  .servicio-fijo h3 {
    font-size: 1.4rem;
  }
}
